import React, { useEffect, useRef, useState } from "react";
import {
  Container,
  Typography,
  Box,
  Card,
  CardContent,
  Button,
  CircularProgress,
  Stack,
  TextField
} from "@mui/material";
import { QRCodeCanvas } from "qrcode.react";

const DualSnapApp: React.FC = () => {
  const [finalImage, setFinalImage] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const videoRef = useRef<HTMLVideoElement>(null);
  const [shareLink, setShareLink] = useState<string | null>(null);
  const frontVideoRef = useRef<HTMLVideoElement>(null);
  const backVideoRef = useRef<HTMLVideoElement>(null);

  const capturePhoto = async (facingMode: "user" | "environment") => {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: { facingMode }
    });

    const video = videoRef.current!;
    video.srcObject = stream;
    await video.play();

    await new Promise((resolve) => setTimeout(resolve, 1000)); // wait for camera

    const canvas = document.createElement("canvas");
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;

    const ctx = canvas.getContext("2d")!;
    ctx.drawImage(video, 0, 0);

    stream.getTracks().forEach((track) => track.stop());
    return canvas.toDataURL("image/png");
  };

  useEffect(() => {
    startVideoStreams();
    if (window.location.hash.startsWith("#img=")) {
      const encoded = window.location.hash.slice(5);
      const decoded = decodeURIComponent(encoded);
      setFinalImage(`data:image/png;base64,${decoded}`);
    }
  }, []);

  const startVideoStreams = async () => {
    try {
      // Prompt for permission first
      await navigator.mediaDevices.getUserMedia({ video: true });

      const devices = await navigator.mediaDevices.enumerateDevices();
      const videoDevices = devices.filter(
        (device) => device.kind === "videoinput"
      );

      const frontCamera =
        videoDevices.find((device) =>
          device.label.toLowerCase().includes("front")
        ) || videoDevices[0];

      const backCamera =
        videoDevices.find((device) =>
          device.label.toLowerCase().includes("back")
        ) ||
        videoDevices[1] ||
        videoDevices[0];

      if (frontCamera && frontVideoRef.current) {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { deviceId: { exact: frontCamera.deviceId } }
        });
        frontVideoRef.current.srcObject = stream;
      }

      if (backCamera && backVideoRef.current) {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { deviceId: { exact: backCamera.deviceId } }
        });
        backVideoRef.current.srcObject = stream;
      }
    } catch (err) {
      console.error("Camera access denied or error:", err);
      alert(
        "⚠️ Please allow camera access in your browser settings to use DualSnap."
      );
    }
  };

  const mergeImages = async (backImage: string, frontImage: string) => {
    const back = new Image();
    const front = new Image();
    back.src = backImage;
    front.src = frontImage;

    await Promise.all([
      new Promise((resolve) => (back.onload = resolve)),
      new Promise((resolve) => (front.onload = resolve))
    ]);

    const canvas = document.createElement("canvas");
    canvas.width = back.width;
    canvas.height = back.height;

    const ctx = canvas.getContext("2d")!;
    ctx.drawImage(back, 0, 0);

    // Add front selfie as small overlay (bottom-right)
    const overlaySize = back.width / 4;
    ctx.beginPath();
    ctx.arc(
      canvas.width - overlaySize / 2 - 20,
      canvas.height - overlaySize / 2 - 20,
      overlaySize / 2,
      0,
      Math.PI * 2
    );
    ctx.clip();
    ctx.drawImage(
      front,
      canvas.width - overlaySize - 20,
      canvas.height - overlaySize - 20,
      overlaySize,
      overlaySize
    );

    return canvas.toDataURL("image/png");
  };

  const handleDualSnap = async () => {
    setLoading(true);
    try {
      const backPhoto = await capturePhoto("environment");
      const frontPhoto = await capturePhoto("user");
      const final = await mergeImages(backPhoto, frontPhoto);
      setFinalImage(final);
      // const base64Image = final?.split(",")[1]; // Remove data:image/png;base64,
      // const encoded = encodeURIComponent(base64Image!);
      // const link = `${window.location.origin}/#img=${encoded}`;
      // setShareLink(link);
    } catch (e) {
      alert("Error capturing images. Please allow camera access.");
    }
    setLoading(false);
  };

  const handleDownload = () => {
    if (!finalImage) return;
    const link = document.createElement("a");
    link.href = finalImage;
    link.download = "dualsnap.png";
    link.click();
  };

  const handleShare = async () => {
    if (!finalImage || !navigator.canShare) return;

    const blob = await (await fetch(finalImage)).blob();
    const file = new File([blob], "dualsnap.png", { type: "image/png" });

    try {
      await navigator.share({
        title: "My DualSnap",
        files: [file]
      });
    } catch (error) {
      alert("Sharing not supported or canceled.");
    }
  };

  return (
    <Container sx={{ textAlign: "center", py: 4 }}>
      {/* Logo */}
      <Box mb={4}>
        <img src="dualsnap-logo.png" alt="DualSnap Logo" width={120} />
        {/* <Typography variant="h5" fontWeight="bold" mt={2}>
          DualSnap
        </Typography> */}
      </Box>

      {/* Hidden Video for Camera Feed */}
      <video ref={videoRef} style={{ display: "none" }} />

      {/* Photo Frame */}
      <Card
        sx={{ maxWidth: 320, margin: "0 auto", borderRadius: 3, boxShadow: 3 }}
      >
        <CardContent>
          {loading ? (
            <CircularProgress />
          ) : finalImage ? (
            <>
              <img
                src={finalImage}
                alt="Final Snap"
                style={{ width: "100%", borderRadius: 10 }}
              />
              <Stack direction="row" spacing={2} justifyContent="center" mt={3}>
                <Button
                  variant="outlined"
                  color="primary"
                  onClick={handleDownload}
                >
                  Download
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleShare}
                  disabled={!navigator.canShare}
                >
                  Share
                </Button>
              </Stack>
            </>
          ) : (
            <Box
              sx={{
                height: 300,
                backgroundColor: "#f5f5f5",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                borderRadius: 2
              }}
            >
              <Typography variant="body2" color="textSecondary">
                Your dual photo will appear here
              </Typography>
            </Box>
          )}
        </CardContent>
      </Card>

      {/* Capture Button */}
      <Button
        variant="contained"
        color="primary"
        sx={{ mt: 4 }}
        onClick={handleDualSnap}
      >
        Take DualSnap
      </Button>

      {/* {shareLink && (
        <Stack spacing={2} mt={2} alignItems="center">
          <TextField
            fullWidth
            label="Share Link"
            value={shareLink}
            InputProps={{ readOnly: true }}
          />
          <Button
            variant="contained"
            onClick={() => navigator.clipboard.writeText(shareLink)}
          >
            📋 Copy Link
          </Button>
          <QRCodeCanvas value={shareLink} />
        </Stack>
      )} */}
    </Container>
  );
};

export default DualSnapApp;
